#square {
  width: 100px;
  height: 100px;
  border: none;
  cursor: pointer;
  background-color: inherit;
  outline: none;
  transition: 0.3s;
  font-size: 30px;
  position: relative;
}

#square:hover {
  scale: 105%;
  transition: 0.3s;
}

#square:focus {
  /* background-color: red; */
  stroke-dashoffset: 0px;
  transition: 1s;
}
