.circle {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
}

.circle circle {
  stroke-dasharray: 200px;
  stroke-dashoffset: 200px;
  animation: circleAni 0.7s;
  animation-fill-mode: forwards;
  width: 100px;
  height: 100px;
  fill: none;
  filter: drop-shadow(0px 0px 5px blue);
}

.circle1 {
  stroke: blue;
  stroke-width: 3;
}

.circle2 {
  stroke: white;
  stroke-width: 2;
}

.circle_ai {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
}

.circle_ai circle {
  stroke-dasharray: 200px;
  stroke-dashoffset: 200px;
  animation: circleAni 0.7s;
  animation-fill-mode: forwards;
  width: 100px;
  height: 100px;
  fill: none;
  filter: drop-shadow(0px 0px 5px blue);
  animation-delay: 0.3s;
}

@keyframes circleAni {
  from {
    stroke-dashoffset: 200px;
  }
  to {
    stroke-dashoffset: 0px;
  }
}
