.board {
  width: 302px;
  height: 302px;
  background-color: inherit;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1px;
  position: relative;
}

.draw {
  border-radius: 50px;
  box-shadow: 0 0 10px 2px white;
  position: absolute;
  background-color: white;
  transition: 0.3s;
}
.red_win {
  border-radius: 50px;
  box-shadow: 0 0 10px 2px red;
  position: absolute;
  background-color: white;
  transition: 0.3s;
}
.blue_win {
  border-radius: 50px;
  box-shadow: 0 0 10px 2px blue;
  position: absolute;
  background-color: white;
  transition: 0.3s;
}

#horizontal1 {
  width: 100%;
  height: 3px;
  top: 100px;
}

#horizontal2 {
  width: 100%;
  height: 3px;
  top: 200px;
}

#vertical1 {
  width: 3px;
  height: 100%;
  left: 100px;
}

#vertical2 {
  width: 3px;
  height: 100%;
  left: 200px;
}
