.Cross {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
}

.Cross {
  fill: none;
  position: absolute;
  animation: alid 0.4s;
  animation-fill-mode: forwards;
  stroke-dasharray: 100px;
  stroke-dashoffset: 100px;
}

#firstCross line {
}

#secondCross {
  animation-delay: 0.2s;
}

.main_Cross {
  stroke: red;
  stroke-width: 2;
  filter: drop-shadow(0px 0px 5px red);
}

.auxiliary_Cross {
  stroke: white;
  stroke-width: 1;
}

@keyframes alid {
  from {
    stroke-dashoffset: 100px;
  }
  to {
    stroke-dashoffset: 0px;
  }
}
