.wrapper {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 30px;
  background-image: url(./UI/images/Background.jpg);
  background-size: cover;
  background-position: 0px -105px;
}

.game_helper {
  color: white;
  font-size: 34px;
}

#red {
  text-shadow: -4px 0 5px red, 0px -4px 5px red, 4px 0 5px red, 0px 4px 5px red;
}

#blue {
  text-shadow: -4px 0 5px blue, 0px -4px 5px blue, 4px 0 5px blue,
    0px 4px 5px blue;
}

#draw {
  text-shadow: 0 0 10px white;
}

#red_win {
  text-shadow: -4px 0 5px red, 0px -4px 5px red, 4px 0 5px red, 0px 4px 5px red;
}

#blue_win {
  text-shadow: -4px 0 5px blue, 0px -4px 5px blue, 4px 0 5px blue,
    0px 4px 5px blue;
}

.btns {
  color: white;
  border: 2px solid white;
  border-radius: 50px;
  box-shadow: 0 0 10px 2px white;
  width: 220px;
  height: 50px;
  font-size: large;
  background-color: inherit;
  cursor: pointer;
  outline: none;
  transition: 0.1s;
  position: relative;
}

.btns:active {
  box-shadow: none;
  width: 215px;
  transition: 0.1s;
}

.btnsDiv {
  display: flex;
  gap: 20px;
}

#playWithFriend {
  box-shadow: 0 0 10px 2px white;
}

#playWithFriend:enabled {
  box-shadow: none;
}

#playWithAi {
  box-shadow: 0 0 10px 2px white;
  padding-left: 25px;
}

#playWithAi:enabled {
  box-shadow: none;
}

.icons {
  position: absolute;
  left: 5px;
  top: 5px;
}
